.leaderBoard{
  background: transparent;
  width: 100%;
  height: 100%;
  pointer-events: none;
  .leaderContainer {
    display: flex;
    flex-direction: column;
    margin: 0;
    min-height: 100vh;
    justify-content: start;
    align-items: center;
    //background-color: #f7f7f7;
    background: transparent;
    pointer-events: none;

    z-index: 1000;
    .leaderBoardTitle{
      background: #dc5f45;
      display: flex;
      font-size: 4rem;
      margin-bottom: 2rem;
    }
    .containerLeaderboard {
      display: flex;
      justify-content: center;
      width: 90%;
      max-width: 1440px;
      margin: 0 auto;
      background: black;

      .profileImg {
        position: relative;
        transition: all 0.3s;

        &:hover {
          transform: translateY(25px);
        }

        img {
          max-width: 100%;
          border-radius: 50%;
          border: 5px solid #f7f7f7;
          filter: drop-shadow(-20px 0 10px rgba(0, 0, 0, 0.1));
          cursor: pointer;

          &:hover {
            + {
              .nameItem {
                opacity: 1;
                transform: translateX(-50%);
                box-shadow: 0 10px 20px rgba(86, 86, 198, 0.3);
              }
            }
          }
        }

        &:not(:first-child) {
          img {
            margin-left: -20px;
          }
        }

        .nameItem {
          position: absolute;
          background-color: #10996d;
          color: #fff;
          font-family: "Bebas Neue", cursive;
          padding: 15px 30px;
          border-radius: 100px;
          bottom: -80px;
          left: 50%;
          white-space: nowrap;
          transform: translate(-50%, -50px);
          letter-spacing: 1px;
          font-size: 20px;
          opacity: 0;
          transition: all 0.3s;

          &:before {
            content: "";
            position: absolute;
            width: 15px;
            height: 15px;
            background-color: #10996d;
            top: 0;
            left: 50%;
            transform: translate(-50%, -50%) rotate(45deg);
          }
        }
      }
    }
  }
}
